import { Component, Element, Listen, Prop, h } from '@stencil/core';
import { debounce } from '../../../utils';

const CHAR_SIZE = 8;

@Component({
  tag: 'mdx-filter-select',
  shadow: true,
})
export class FilterSelect {
  @Prop()
  displayedLabel: string;

  @Prop()
  badge: string;

  @Element()
  host: HTMLMdxFilterSelectElement;

  @Prop({ reflect: true })
  label!: string;

  @Prop()
  selectedOptions: string[];

  @Prop()
  clearButtonAriaLabel?: string;

  @Prop({ reflect: true })
  open?: boolean;

  @Listen('selectClear', { capture: true })
  handleClick() {
    this.selectedOptions = [];
  }

  @Listen('resize', { capture: true, target: 'window' })
  onWindowResize() {
    this.recalculateButtonLabel();
  }

  componentDidRender() {
    this.recalculateButtonLabelFn();
  }

  private recalculateButtonLabel = debounce(this.recalculateButtonLabelFn.bind(this), 100);
  private recalculateButtonLabelFn() {
    if (this.selectedOptions && this.selectedOptions.length === 0) {
      this.displayedLabel = this.label;
      this.badge = '';

      return;
    }

    const buttonLabel = this.host.shadowRoot
      ?.querySelector('mdx-filter-select-button')
      ?.shadowRoot?.querySelector('.filter-select-button__button-label');
    let width = buttonLabel ? buttonLabel.clientWidth - 20 : 100;

    const displayedLabels: string[] = [];
    while (width > 0) {
      if (
        this.selectedOptions[displayedLabels.length] &&
        (this.selectedOptions[displayedLabels.length].length + 2) * CHAR_SIZE < width
      ) {
        width -= (this.selectedOptions[displayedLabels.length].length + 2) * CHAR_SIZE;
        displayedLabels.push(this.selectedOptions[displayedLabels.length]);
      } else {
        break;
      }
    }

    this.displayedLabel = displayedLabels.join(', ') + (displayedLabels.length < this.selectedOptions.length ? ', ...' : '');
    this.badge =
      displayedLabels.length < this.selectedOptions.length
        ? '+' + (this.selectedOptions.length - displayedLabels.length).toString()
        : '';
  }

  render() {
    return this.selectedOptions.length > 0 ? (
      <mdx-filter-select-button
        badge={this.badge}
        label={this.displayedLabel}
        clearButtonAriaLabel={this.clearButtonAriaLabel}
      />
    ) : (
      <mdx-select-button open={this.open} text={this.label} />
    );
  }
}
